import authorHead from '../../components/authorHead'
import {
	api
} from '../../const'
import {
	checkT
} from '../../utils/net'
import login from '../../components/login'

export default {
	data() {
		return {
			loginVisible: false,
			user: {}
		}
	},
	components: {
		authorHead,
		login
	},
	methods: {
		setListFrom(e) {
			uni.setStorageSync('fromItem', e.target.dataset.item)
		},
		closeModalEvent() {
			this.loginVisible = false
			this.getData()
		},
		login(){
			this.loginVisible = true
		},
		async getData() {
			const t = uni.getStorageSync('t')
			var that = this;
			this.$http({
				act: 'user.info',
				t: t
			}, function(res) {
				that.user = res.data
			})
		},
		cancelModalClose: function(){
			this.loginVisible = false
		}
	},
	onShow() {
		var that = this
		var t = uni.getStorageSync("t")
		checkT(t,
			function() {
				// that.loginVisible = true
			},
			function() {
				that.getData()
			}
		);
	}
}
